<template>
  <v-container>
    <div>
      <v-card class="mx-auto" width="1200px">
        <div v-if="textReady" class="bk">
          <div
            v-for="content in textNorm"
            v-bind:key="content"
            class="textleft"
          >
            {{ content }}
          </div>
        </div>
      </v-card>
    </div>

    <v-col class="boutonsContainer">
      <v-btn
        v-if="!started"
        height="80px"
        elevation="24"
        x-large
        class="singleBtn"
        width="270px"
        color="warning"
        @click="start()"
        ><v-icon large>mdi-step-forward</v-icon>Commencer</v-btn
      >
      <v-btn
        v-else
        height="80px"
        disabled
        x-large
        class="singleBtn"
        width="270px"
        color="warning"
        ><v-icon large>mdi-step-forward</v-icon>Commencer</v-btn
      >
      <v-btn
        v-if="started && !resultDisplay"
        height="80px"
        elevation="24"
        x-large
        class="singleBtn"
        width="270px"
        color="success"
        @click="over()"
        ><v-icon large>mdi-check-bold</v-icon>J'ai fini</v-btn
      >
      <v-btn
        v-else-if="resultDisplay || !started"
        disabled
        height="80px"
        x-large
        class="singleBtn"
        width="270px"
        color="success"
        @click="over()"
        ><v-icon large>mdi-check-bold</v-icon>J'ai fini</v-btn
      >
    </v-col>
    <div class="flex-container">
      <div class="logo">
        <v-img
          max-height="100"
          max-width="100"
          src="../assets/logo.png"
        ></v-img>
      </div>

      <v-card v-if="resultDisplay" class="result">
        {{ result }}
      </v-card>
      <v-btn
        v-if="resultDisplay"
        @click="reload()"
        class="ma-2"
        color="primary"
        elevation="24"
        dark
        height="50px"
        ><v-icon large>mdi-refresh</v-icon></v-btn
      >
    </div>
  </v-container>
</template>


<script>
export default {
  name: "Main",
  data() {
    return {
      textId: this.$route.params.textId,
      text: "",
      textNorm: [],
      textReady: false,
      realTime: 0,
      startTime: null,
      stopCalling: false,
      minutes: 0,
      started: false,
      result: "",
      resultDisplay: false,
      textLength: 0,
    };
  },
  created: () => {
    console.log('Credit to Abelardo Gonzalez for the font "open-dyslexic"');
  },
  async mounted() {
    this.text = (
      await this.$axios({
        method: "get",
        url: "api/text/" + this.textId,
      })
    ).data;
    this.textNormaliser();
    this.textReady = true;
  },
  methods: {
    textNormaliser: function () {
      let tabText = this.text.split(" - ");
      this.textLength = tabText.length;
      let tabcpt = 0;
      let caracCpt = 0;
      this.textNorm[0] = "";
      for (let i = 0; i < tabText.length; i++) {
        caracCpt += tabText[i].length + 3;
        this.textNorm[tabcpt] += tabText[i];
        if (i !== tabText.length - 1) {
          this.textNorm[tabcpt] += " -";
        }
        if (caracCpt >= 60) {
          tabcpt++;
          this.textNorm[tabcpt] = "";
          caracCpt = 0;
        } else {
          this.textNorm[tabcpt] += " ";
        }
      }
    },
    start: function () {
      this.started = true;
      this.$nextTick(function () {
        window.setInterval(() => {
          if (this.stopCalling) {
            return null;
          }
          this.calculateTime();
        }, 1000);
      });
    },
    calculateTime: function () {
      this.realTime++;
      console.log(this.realTime);
    },
    over: function () {
      this.stopCalling = true;
      
      if (this.realTime < 15) {
        this.result = "Impossible, recommence !";
      } else {
        this.result = 60 / this.realTime;
        this.result = this.textLength * this.result;
        this.result =
          "Tu as lu " + Math.ceil(this.result) + " mots par minute !";
      }
      this.resultDisplay = true;
    },
    reload: function () {
      window.location.reload();
    },
  },
};
</script>


